@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap");
*, ::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: "Inter", sans-serif;
  background-color: #e5ecf4;
  line-height: 1.6;
  user-select: none;
}

main {
  margin: 1rem;
}

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

.mb-0 {
  margin-bottom: 0;
}

.mb-1 {
  margin-bottom: 1rem;
}

.mb-2 {
  margin-bottom: 2rem;
}

.mb-3 {
  margin-bottom: 3rem;
}

.mb-4 {
  margin-bottom: 4rem;
}

.mb-5 {
  margin-bottom: 5rem;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

.logo {
  display: block;
  width: 150px;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.avatar {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 50%;
}

.illustration {
  width: 400px;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.fluid {
  max-width: 100%;
  height: auto;
}

.loading-screen {
  position: fixed;
  inset: 0;
  overflow: hidden;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: #e5ecf4;
}
.loading-screen .line-of-dots {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  transform: translate(-50%, -50%);
  animation: line-of-dots 1s infinite ease backwards;
}

.overlay-timer {
  position: relative;
}
.overlay-timer::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: 1;
  background-color: var(--bg-color, rgba(0, 0, 0, 0.5));
}
.overlay-timer .count {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  font-size: clamp(4rem, 6vw, 10rem);
  font-weight: 500;
  color: #f5f5f5;
  transform: translate(-50%, -50%);
}

@keyframes line-of-dots {
  0% {
    box-shadow: -30px 10px 0 -10px #726bea, -15px 10px 0 -10px #726bea, 0 10px 0 -10px #726bea, 15px 10px 0 -10px #726bea, 30px 10px 0 -10px #726bea;
  }
  10% {
    box-shadow: -30px 10px 0 0 #726bea, -15px 10px 0 -10px #726bea, 0 10px 0 -10px #726bea, 15px 10px 0 -10px #726bea, 30px 10px 0 -10px #726bea;
  }
  20% {
    box-shadow: -30px 10px 0 0 #726bea, -15px 10px 0 0 #726bea, 0 10px 0 -10px #726bea, 15px 10px 0 -10px #726bea, 30px 10px 0 -10px #726bea;
  }
  30% {
    box-shadow: -30px 10px 0 0 #726bea, -15px 10px 0 0 #726bea, 0 10px 0 0 #726bea, 15px 10px 0 -10px #726bea, 30px 10px 0 -10px #726bea;
  }
  40% {
    box-shadow: -30px 10px 0 0 #726bea, -15px 10px 0 0 #726bea, 0 10px 0 0 #726bea, 15px 10px 0 0 #726bea, 30px 10px 0 -10px #726bea;
  }
  50% {
    box-shadow: -30px 10px 0 0 #726bea, -15px 10px 0 0 #726bea, 0 10px 0 0 #726bea, 15px 10px 0 0 #726bea, 30px 10px 0 0 #726bea;
  }
  60% {
    box-shadow: -30px 10px 0 -10px #726bea, -15px 10px 0 0 #726bea, 0 10px 0 0 #726bea, 15px 10px 0 0 #726bea, 30px 10px 0 0 #726bea;
  }
  70% {
    box-shadow: -30px 10px 0 -10px #726bea, -15px 10px 0 -10px #726bea, 0 10px 0 0 #726bea, 15px 10px 0 0 #726bea, 30px 10px 0 0 #726bea;
  }
  80% {
    box-shadow: -30px 10px 0 -10px #726bea, -15px 10px 0 -10px #726bea, 0 10px 0 -10px #726bea, 15px 10px 0 0 #726bea, 30px 10px 0 0 #726bea;
  }
  90% {
    box-shadow: -30px 10px 0 -10px #726bea, -15px 10px 0 -10px #726bea, 0 10px 0 -10px #726bea, 15px 10px 0 -10px #726bea, 30px 10px 0 0 #726bea;
  }
  100% {
    box-shadow: -30px 10px 0 -10px #726bea, -15px 10px 0 -10px #726bea, 0 10px 0 -10px #726bea, 15px 10px 0 -10px #726bea, 30px 10px 0 -10px #726bea;
  }
}
.card {
  position: relative;
  overflow: hidden;
  height: fit-content;
  padding: 2rem;
  background-color: #f8f8f8;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  text-align: center;
}
.card-title, .card .qr-name {
  font-size: clamp(0.875rem, 4vw, 1.75rem);
  font-weight: 500;
  color: #29335c;
}
.card-title.success, .card .success.qr-name {
  margin-top: 1.5rem;
  color: #559e32;
}
.card-title.success::before, .card .success.qr-name::before {
  content: "✓";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: block;
  width: 40px;
  height: 40px;
  margin: 1rem auto;
  font-size: 1.55rem;
  background-color: #559e32;
  color: #f8f8f8;
  border-radius: 50%;
}
.card-title.failed, .card .failed.qr-name {
  margin-top: 1.5rem;
  color: #d33329;
}
.card-title.failed::before, .card .failed.qr-name::before {
  content: "X";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: block;
  width: 40px;
  height: 40px;
  margin: 1rem auto;
  font-size: 1.55rem;
  background-color: #d33329;
  color: #f8f8f8;
  border-radius: 50%;
}
.card-subtitle {
  font-size: clamp(0.75rem, 4vw, 1.125rem);
  font-weight: 400;
  color: #66717e;
}
.card .user-details {
  text-align: start;
}
.card .user-details p {
  font-size: clamp(0.75rem, 4vw, 1.125rem);
  font-weight: 400;
  color: #66717e;
}
.card .user-details p span {
  color: #29335c;
}
.card .qr-code img {
  max-width: 100%;
  height: auto;
  margin: 0 auto;
}
.card .qr-name {
  font-size: clamp(0.875rem, 6vw, 1.75rem);
}
.card-info {
  font-size: clamp(0.688rem, 4vw, 0.938rem);
  font-weight: 400;
  color: #66717e;
}
.card-info span {
  color: #726bea;
}
.card.active {
  animation: fade-in-right 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@keyframes fade-in-right {
  0% {
    transform: translateX(50px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
.progress-bar {
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  counter-reset: step;
}
.progress-bar li {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
  font-size: 0.889rem;
  font-weight: 400;
  color: #66717e;
}
.progress-bar li::before {
  content: counter(step);
  counter-increment: step;
  margin-right: 0.3rem;
  padding: 0 0.5em;
  background-color: #66717e;
  color: #f8f8f8;
  border-radius: 5px;
}
.progress-bar li:not(:first-child)::before {
  margin-left: 0.3rem;
}
.progress-bar li:not(:last-child):after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1.5px;
  background-color: #66717e;
}
.progress-bar li.step.active {
  color: #726bea;
}
.progress-bar li.step.active::before {
  background-color: #726bea;
}

.input-group {
  position: relative;
  margin-bottom: 1.5rem;
  text-align: start;
}

.form-control {
  width: 100%;
  padding: 0.8em 1em;
  font-size: clamp(0.75rem, 4vw, 1rem);
  background: none;
  outline: none;
  color: #29335c;
  caret-color: #726bea;
  border: 1px solid #29335c;
  border-radius: 5px;
}
.form-control:focus {
  border-color: #726bea;
}
.form-control.invalid {
  margin-bottom: 0.2rem;
  border-color: #d33329;
}

.form-label {
  position: absolute;
  left: 0;
  padding: 0.6em 1em;
  font-size: clamp(0.75rem, 4vw, 1rem);
  color: #66717e;
  pointer-events: none;
  transition: 150ms cubic-bezier(0.4, 0, 0.2, 1);
}

.form-control:focus ~ .form-label {
  padding: 0 0.5em;
  background-color: #f8f8f8;
  color: #726bea;
  transform: translateY(-50%) scale(0.8);
}

.form-control:not(:placeholder-shown).form-control:not(:focus) ~ .form-label {
  padding: 0 0.5em;
  background-color: #f8f8f8;
  color: #29335c;
  transform: translateY(-50%) scale(0.8);
}

.invalid-feedback {
  font-size: 0.8rem;
  color: #d33329;
}

.btn {
  font: inherit;
  background: none;
  color: inherit;
  border: none;
  outline: none;
}
.btn-primary, .btn-success {
  width: 100%;
  padding: 0.5em 1em;
  font-size: clamp(0.75rem, 4vw, 1rem);
  font-weight: 400;
  background-color: #726bea;
  color: #f5f5f5;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.5s ease-out;
}
.btn-success {
  background-color: #559e32;
}
.btn-outline-primary {
  width: 100%;
  padding: 0.5em 1em;
  font-size: clamp(0.75rem, 4vw, 1rem);
  font-weight: 400;
  border: 1px solid #726bea;
  color: #726bea;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.5s ease-in;
}

@media (hover: hover) {
  .btn-primary:hover, .btn-success:hover {
    transform: translateY(-7px);
  }
  .btn-outline-primary:hover {
    background-color: #726bea;
    color: #f5f5f5;
  }
}